<template>
  <div id="address-search-view">
    <h2>Address Search Page</h2>
    <p>Input Postal Code Here.</p>
    <input
      type="number"
      v-model="postalCodeFirst"
      @input="sliceMaxLength(true, 3)"
    />-
    <input
      type="number"
      v-model="postalCodeSecond"
      @input="sliceMaxLength(false, 4)"
    />
    <button @click="searchByPostCode">Search</button><br />
    <button @click="isShowAddressSearch = !isShowAddressSearch">
      お住まいの住所から検索する
    </button>
    <table border="1" v-if="isShowAddressSearch">
      <tbody></tbody>
    </table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isShowAddressSearch: false,
      postalCodeFirst: "",
      postalCodeSecond: "",
      kenData: [],
      cityData: [],
      townData: [],
    };
  },
  methods: {
    searchByPostCode: function () {
      console.log(this.postalCodeFirst + "-" + this.postalCodeSecond);
    },
    sliceMaxLength: function (isFirstText, maxLength) {
      if (isFirstText) {
        this.postalCodeFirst = this.postalCodeFirst.slice(0, maxLength);
      } else {
        this.postalCodeSecond = this.postalCodeSecond.slice(0, maxLength);
      }
    },
  },
};
</script>
<style scoped>
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>